import {mapState, mapMutations} from 'vuex'
export default {
	data() {
		return {
			account:'',
			name:'',
			takePassword:'',
			codeBut:'发送验证码',
			codeClick:true,
			code:'',
			takeType:0,
			takeTypeList:[]
		}
	},
	computed:{
		...mapState({
			mobilephone:state=>state.user.mobilephone
		}),
		getVaguePhoneNumber(){
			var array = this.mobilephone
			var mphone = array.substring(0, 3) + '****' + array.substring(7)

			return mphone
		}
	},
	onLoad() {
		this.__init()
	},
	methods: {
		__init(){
			var _self = this
			uni.showLoading({title:'', mask:true})
			_self.$H.post('/api/TakeAccount/TakeTypeList',{},{
				token:true
			}).then(res=>{	
				console.log(res)
				uni.hideLoading()
				if(res.status == 0){
					var temp = res.data
					for (var i = 0; i < temp.length; i++) {
						var list = {}
						list.Text = temp[i].Text
						list.Value = temp[i].Value
						_self.takeTypeList.push(list)
					}
				}else{
					uni.showToast({title:res.message, icon:'none', duration:1000})
				}
			})
		},
		accountInput(e){
			this.account = e.detail.value
		},
		nameInput(e){
			this.name = e.detail.value
		},
		passwordInput(e){
			this.takePassword = e.detail.value
		},
		phoneCodeInput(e){
			this.code = e.detail.value
		},
		changeTakeType(index){
			this.takeType = index
		},
		bindAccount(){
			var _self = this
			if(_self.account == ''){
				uni.showToast({title: '请输入账号', icon:'none', duration: 1500})
				return
			}
			if(_self.name == ''){
				uni.showToast({title: '请输入姓名', icon:'none', duration: 1500})
				return
			}
			if(_self.takePassword == ''){
				uni.showToast({title: '请输入提现密码', icon:'none', duration: 1500})
				return
			}
			if(_self.code == ''){
				uni.showToast({title: '请输入验证码', icon:'none', duration: 1500})
				return
			}
			uni.showModal({
			    title: '确认绑定账号',
			    content: _self.account,
			    success: function (res) {
			        if (res.confirm) {
			            _self.sureBind()
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		sureBind(){
			var _self = this
			uni.showLoading({title:'', mask:true})
			var typeValue = _self.takeTypeList[_self.takeType].Value
			_self.$H.post('/api/TakeAccount/Add',{
				Type:typeValue,
				Account:_self.account,
				Name:_self.name,
				TakePassword:_self.takePassword,
				MobilephoneCode:_self.code
			},{
				token:true
			}).then(res=>{	
				console.log(res)
				uni.hideLoading()
				if(res.status == 0){
					uni.showToast({title: '绑定成功', icon:'none', duration: 1500})
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					}, 1500)
				}else{
					uni.showToast({title:res.message, icon:'none', duration:1000})
				}
			})
		},
		sendphoneCode(){
			var _self = this
			if(_self.codeClick){
				//发送验证码
				_self.sendCode()
				_self.codeClick = false
				// 倒计时开始
				var daojishi = 60
				_self.codeBut = "重新发送" + daojishi;
				_self.timer = setInterval(function(){
					daojishi--
					_self.codeBut = "重新发送" + daojishi;
					if(daojishi <= 0){
						_self.codeBut = "重新发送"
						_self.codeClick = true
						clearInterval(_self.timer);
					}
				}, 1000)
			}
		},
		sendCode(){
			var _self = this
			_self.$H.post('/api/TakeAccount/SendCode',{},{
				token:true
			}).then(res=>{
				if(res.status === 0){
					
				}else{
					uni.showToast({title:res.message, icon:'none', duration:1000})
				}
			})
		}
	}
}
